<template>
  <div class="container" v-loading="isLoading">
    <div class="App">

      <el-card>

        <div style="display: flex; justify-content: end; align-items: center;">

          <el-select v-model="languagesSelected" filterable>
            <el-option
                v-for="item in languagesOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>

          <el-button @click="downloadShell" type="primary">Download</el-button>
          <el-button @click="uploadGooglePlay" :loading="isUploadLoading" type="primary">Upload to Google Play</el-button>
          <el-button @click="addDialogVisible = true" type="primary">Add Screenshot</el-button>

        </div>


<!--        <el-button @click="exportImages">English</el-button>-->
        <div v-for="languageItem in languagesOptions">
          <span style="margin: 12px">{{ languageItem.label }}</span>
          <div style="overflow-x: auto">
            <screen-canvas ref="canvasRefs" v-for="(item, i) in pageData" :key="i"
                           canvas-width="1080" canvas-height="1920"
                           style="margin: 12px"
                           :text="getLanguageParam(item, languageItem.value)"
                           :background-color="item.ssBgColor"
                           frame-img-url="/frames/google-pixel-4-xl-black.png"
                           :screen-url="item.ssAppUrl"></screen-canvas>
          </div>
        </div>
<!--        <span style="margin: 12px">{{ getLanguageName() }}</span>-->
<!--        <div style="overflow-x: auto">-->
<!--          <screen-canvas ref="canvasRefs" v-for="(item, i) in pageData" :key="i"-->
