<!--              :background-color="item.ssBgColor"-->
<!--              frame-img-url="/frames/google-pixel-4-xl-black.png"-->
<!--              :screen-url="item.ssAppUrl"></screen-canvas>-->
<!--        </div>-->


<!--        <screen-canvas canvas-width="1080" canvas-height="1920"-->
<!--                       style="margin: 12px"-->
<!--                       background-color="#3ba7c1"-->
<!--                       text="Update \n your productivity"-->
<!--                       frame-img-url="/frames/google-pixel-4-xl-black.png"-->
<!--                       screen-url="https://play-lh.googleusercontent.com/TL8pKfZFU-iGKozB68b8IUYJ_riBQb7E1F55QtIqOe1PQGGm8Cydsw9lY_uvZCbwGYEn=w5120-h2880-rw"></screen-canvas>-->


      </el-card>
    </div>

    <el-dialog
        title="Add Screenshot"
        :visible.sync="addDialogVisible"
        width="30%">

      <el-form ref="screenshotFrom" :model="screenshotFrom">
        <el-form-item label="String ID" prop="screenshot">
          <el-input v-model="screenshotFrom.transId"></el-input>
        </el-form-item>
        <el-form-item label="Screenshot Url" prop="screenshot">
          <el-input v-model="screenshotFrom.ssAppUrl"></el-input>
        </el-form-item>
        <el-form-item label="Background Color(eg: #0000000)" prop="screenshot">
          <el-input v-model="screenshotFrom.ssBgColor"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
              <el-button @click="addDialogVisible = false">Cancel</el-button>
              <el-button type="primary" @click="handAddScreenshot">Add</el-button>
            </span>
    </el-dialog>


  </div>
</template>

<script>

import languages from "@/store/languages";
import ScreenCanvas from "@/components/ScreenCanvas";
import {getScreenshots, addScreenshots , uploadGoogle} from '../../api/screenshot'
import FileSaver from 'file-saver'

export default {
  components: {ScreenCanvas},
  data() {
    return {
      isLoading: false,
      addDialogVisible: false,
      isUploadLoading: false,
      languagesOptions: [],
      languagesSelected: "EN",
      pageData: [],
      screenshotFrom: {
        projectId: this.$route.params.id,
        transId: 0,
        ssAppUrl: '',
        ssBgColor: ''
      },

    };
  },
  computed: {

  },
  watch: {
    projectData(newVal, oldVal) {
      if (this.$route.params.id == this.$store.state.tab.projectData.projectId) {
        this.refreshPage()
      }
    },
    languagesSelected(newVal, oldVal) {
      if (newVal != oldVal) {
        this.pageData = this.pageData
      }
    }
  },
  mounted() {
    this.refreshPage()
  },
  methods: {

    refreshPage() {
      // 构建语言选择列表
      const projectData = this.$store.state.tab.projectData;
      if (this.$route.params.id != projectData.projectId) {
        // 没有基础数据，暂时不处理
        return
      }

      const set = []
      projectData.languages.split(",").forEach((item) => {
        set.push(item.trim())
      });

      this.languagesOptions = []

      Object.keys(languages.state.allLanguages).forEach((item) => {
        if (set.includes(languages.state.languageKey[item])) {
          this.languagesOptions.push({
            label: languages.state.allLanguages[item],
            value: languages.state.languageKey[item],
          })
        }
      })

      const request = {
        "projectId": this.$route.params.id
      }
      getScreenshots(request).then((data) => {
        this.isLoading = false
        console.log(data)
        this.pageData = data.data
      })
    },
    getLanguageParam(data, key) {
      let result = ""
      console.log(key)
      Object.keys(languages.state.languageKey).forEach((item) => {
        if (key === languages.state.languageKey[item]) {
          result = data.transKeys[item]
        }
      })
      return result
    },
    getLanguageName() {
      return languages.state.languages[this.languagesSelected]
    },
    handAddScreenshot() {
      addScreenshots(this.screenshotFrom).then((data) => {
        console.log(data)
        this.addDialogVisible = false
      })
    },

    uploadGooglePlay() {
      this.isUploadLoading = true
      let files = new Array(this.$refs.canvasRefs.length)
      for (let i = 0; i < this.$refs.canvasRefs.length; i++) {
        files[i] = this.$refs.canvasRefs[i].exportImages()
      }

      let formData = new FormData()
      Array.from(files).forEach(file => {
        formData.append("files", file)
      })
      formData.append("projectId", this.$route.params.id)
      formData.append("languages", this.languagesSelected)

      uploadGoogle(formData).then((data) => {
        this.isUploadLoading = false
        console.log(data)
      })
    },
    formatImageUrl(data) {
      return this.require("/assets/logo.png")
    },
    downloadShell() {
      var data = '#!/bin/bash\n'
      data += "adb devices";
      const blob = new Blob([data], {type: "text/plain;charset=utf-8"})
      FileSaver.saveAs(blob, "applocalize_gen_screenshot.bat")
    }

  },
}
</script>


